import React from 'react'
import { useState, useImperativeHandle, forwardRef } from "react";
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';

const AlertDialog = (props, ref) => {
    const [open, setOpen] = useState(false);
    const [title, setTitle] = useState('');
    const [message, setMessage] = useState('');
    const [flg, setFlg] = useState(false);
    const [cancelFlg, setCancelFlg] = useState(false);

    // ,title,message
    const ErrorMessage = [{ id: 'ad000-e001', title: '', message: '' },
    { id: 'ad000-e001', title: '', message: '' },
    { id: 'ad000-e001', title: '', message: '20' },
    { id: 'ad001-e001', title: '', message: '' },
    { id: 'tc001-e001', title: 'ID', message: 'ID' },
    { id: 'tc001-e002', title: '', message: '' }
    ]
    
    const InfoMessage = [{ id: 'ad000-i001', title: '', message: '' },
    { id: 'ad000-i001', title: '', message: '' },
    { id: 'ad000-i001', title: '', message: '' },
    { id: 'ad001-i001', title: '', message: '' },
    { id: 'ad001-i001', title: '', message: '' }
    ]

    const QuestionMessage = [{ id: 'ad000-q001', title: '', message: '' },
    { id: 'ad000-q001', title: '', message: '' },
    { id: 'ad000-q001', title: '', message: '' },
    { id: 'ad999-q999', title: '', message: '' }
    ]

    useImperativeHandle(ref, () => ({
        MessageOpen: (ref) => {
            console.log(ref)
            if (flg == false) {
                let messageType = ref.substr(6, 1);
                setCancelFlg(messageType)

                let message
                if (messageType == 'e') {
                    message = ErrorMessage.filter(m => {
                        return m.id === ref
                    });
                } else if (messageType == 'i') {
                    message = InfoMessage.filter(m => {
                        return m.id === ref
                    });
                } else if (messageType == 'q') {
                    message = QuestionMessage.filter(m => {
                        return m.id === ref
                    });
                }
                // console.log(message)
                setTitle(message[0].title)
                setMessage(message[0].message)
                setOpen(true)
                setFlg(true)
            }
        }
    }))

    const dialogclose = () => {
        setOpen(false);
        setFlg(false)
    }

    return (
        <div>
            <Dialog
                open={open}
                onClose={dialogclose}
                aria-labelledby="alert-dialog-title"
                aria-describedby="alert-dialog-description"
            >
                <DialogTitle id="alert-dialog-title">
                    {title}
                </DialogTitle>
                <DialogContent>
                    <DialogContentText id="alert-dialog-description">
                        {message}
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button onClick={dialogclose}>OK</Button>
                    {(() => {
                        if (cancelFlg == 'q') {
                            return <Button onClick={dialogclose} autoFocus></Button>;
                        }
                    })()}
                    {/* <Button onClick={dialogclose} autoFocus></Button> */}
                </DialogActions>
            </Dialog>
        </div>
    )
}
export default forwardRef(AlertDialog)